import { BrowserModule } from '@angular/platform-browser';
import {APP_INITIALIZER, NgModule} from '@angular/core';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { RouterTestingModule } from '@angular/router/testing';
import { NavigationComponent } from './main-page/navigation/navigation.component';

import { ModeToggleService } from '../services/modeToggle.service';
import { StorageService } from '../services/local-storage.service';
import { MainPageComponent } from './main-page/main-page.component';
import { ThumbnailComponent } from './thumbnail/thumbnail.component';
import { ProjektGalerieComponent } from './projekt-galerie/projekt-galerie.component';
import { AboutMeComponent } from './about-me/about-me.component';
import { SkillsComponent } from './skills/skills.component';
import { KontaktComponent } from './kontakt/kontakt.component';
import { WebentwicklungComponent } from './projekte/webentwicklung/webentwicklung.component';
import { BlogComponent } from './projekte/webentwicklung/blog/blog.component';
import { VereinComponent } from './projekte/webentwicklung/verein/verein.component';



export function themeFactory(themeService: ModeToggleService) {
  return () => themeService.setThemeOnStart();
}

@NgModule({
  declarations: [
    AppComponent,
    NavigationComponent,
    MainPageComponent,
    ThumbnailComponent,
    ProjektGalerieComponent,
    AboutMeComponent,
    SkillsComponent,
    KontaktComponent,
    BlogComponent,
    VereinComponent,
    WebentwicklungComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterTestingModule
  ],
  providers: [
    ModeToggleService, StorageService,
    {provide: APP_INITIALIZER, useFactory: themeFactory, deps: [ModeToggleService], multi: true}
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
